'useClient'

import InputField from "@/components/ui/input/input";
import Image from "next/image";
import '../../../styles/global.css';
import { Button } from "@chakra-ui/react";
import CustomButton from "@/components/ui/button/customButton";

export default function LoginForm() {
    const imageUrl = "/images/login-background.png"
    const imageUrlAmbylon = "/images/ambylon-banner.png"
    return (
        <div className="grid  sm:grid-cols-3 h-screen w-full ">
            {/* Sidebar (2/3 on desktop) */}
            <div className="hidden sm:block sm:col-span-2 bg-blue-100 overflow-hidden">
                <Image
                    className="!relative "
                    src={imageUrl}
                    alt="Login Background"
                    layout="fill"
                    objectFit="cover"
                    quality={100}
                    priority
                />
            </div>

            {/* Main Content (1/3 on desktop) */}
            <div className="sm:col-span-1 bg-[#181818]  mx-20">
                <div className="h-full flex flex-col items-center justify-center">
                    <div className="bg-amber-300 h-[70%] flex flex-col  w-[60%] ">
                        <div className=" h-[85.54px] w-[396]">
                            <Image
                                src={imageUrlAmbylon}
                                 className="!relative "
                                alt="Login Background"
                                layout="fill"
                                objectFit="contain" // Adjust as needed: 'contain', 'cover', etc.
                                quality={100}
                                priority
                            />
                        </div>
                       <div>
                        
                       </div>
                        <div>

                        </div>
                        <div>
                            <CustomButton>Login</CustomButton>
                        </div>
                    </div>
                </div>

            </div>
        </div>

    );
} 